.section4__center__circle {
  background-color: var(--main-color);
  border-radius: 180;
  width: 160px;
  height: 160px;
  position: absolute;
  top: 560;
  left: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section4__center__circle__text {
  font-family: var(--title-font);
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  text-transform: uppercase;
  color: var(--light-text-color);
}
